/* wrapper for menubar regions */
.menubar {

	width:100%;
	position:fixed;
	top:0;
	left:0;
	z-index:100;
	background:white;

	a, a:link, a:visited{
		height: $base-row-height;
		line-height: $base-row-height;
	}

	a:hover{
		text-decoration: none;
	}

}

	.menubar-header{

		a, a:link, a:visited{
			display: block;
		}
	}

	.menubar-primary{
		/*display:none;*/
	}

	.menubar-secondary{
		
		/* debug */
		/*background: #f5f5f5;*/

		a, a:link, a:visited{
			display: inline-block;
			width: $base-row-height;
			float:right;
		}



	}

